.card {
    margin-bottom: 0px;
}

.card-title-icon {
    margin-bottom: 12px;
    height: 50px;
}

.card-btn-primary {
  color: #fff;
  background-color: transparent;
  background-image: none;
  border-color: #fff;
}

.card-btn-primary:hover {
  color: #2bb3f3;
  background-color: #fff;
  border-color: #0d9bde;
}

.card-btn-primary:focus, .card-btn-primary.focus {
  box-shadow: 0 0 0 0.2rem rgba(43, 179, 243, 0.5);
}

.card-btn-success {
  color: #fff;-
  background-color: transparent;
  background-image: none;
  border-color: #fff;
}

.card-btn-success:hover {
  color: #47bc00;
  background-color: #fff;
  border-color: #348900;
}

.card-btn-success:focus, .card-btn-success.focus {
  box-shadow: 0 0 0 0.2rem rgba(71, 188, 0, 0.5);
}

.card-btn-secondary {
  color: #fff;
  background-color: transparent;
  background-image: none;
  border-color: #fff;
}

.card-btn-secondary:hover {
  color: #868e96;
  background-color: #fff;
  border-color: #6c757d;
}

.card-btn-secondary:focus, .card-btn-secondary.focus {
  box-shadow: 0 0 0 0.2rem rgba(134, 142, 150, 0.5);
}